<template>
  <div id="app" class="AppComponent">
    <div class="Viewer" v-if="selected.view === 'viewer'">
      <pre>{{ sourceData }}</pre>
    </div>
    <textarea class="Editor" v-model="sourceCode" v-if="selected.view === 'editor'" autofocus :ref="'agendator-editor'"></textarea>
    <div class="BottomButton ToggleViewButton" v-on:click="nextSelectedView()">
      <span v-if="selected.view === 'editor'">  &#945;</span>
      <span v-else-if="selected.view === 'viewer'">&#937;</span>
    </div>
    <div class="Error" v-if="sourceError" v-on:click="clearError()">{{sourceError}}</div>
  </div>
</template>

<script>

import Agendator from './agendator.parser.js'

const saveState = state => {
  localStorage.AGENDATOR_APP_STATE = JSON.stringify(state);
};

const loadState = () => {
  if(!("AGENDATOR_APP_STATE" in localStorage)) {
    localStorage.AGENDATOR_APP_STATE = JSON.stringify({sourceCode:""});
  }
  return JSON.parse(localStorage.AGENDATOR_APP_STATE);
}

export default {
  name: 'app',
  components: {},
  data() {
    return {
      sourceData: {},
      sourceCode: "",
      sourceError: null,
      selectable: {
        views: ["editor", "viewer"]
      },
      selected: {
        view: "editor"
      }
    }
  },
  created() {
    this.sourceCode = loadState().sourceCode;
  },
  methods: {
    nextSelectedView() {
      const currentIndex = this.selectable.views.indexOf(this.selected.view);
      if((currentIndex === -1) || ((currentIndex + 1) >= this.selectable.views.length)) {
        this.selected.view = this.selectable.views[0];
      } else {
        this.selected.view = this.selectable.views[currentIndex+1];
      }
      if(this.selected.view === "viewer") {
        setTimeout(() => this.$refs["agendator-editor"].focus(), 1000);
      }
    },
    clearError() {
      this.sourceError = null;
    }
  },
  watch: {
    sourceCode(newValue) {
      try {
        this.sourceData = Agendator.parse(newValue);
        this.sourceError = null;
        saveState({sourceCode:newValue});
      } catch(error) {
        this.sourceError = error;
      }
    }
  }
}
</script>

