  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  border: 0px dashed white;
  width: 100%;
  height: 100%;
  background-color: #222;
}
.AppComponent {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin: 0px;
  padding: 0px;
}
.AppComponent .Editor {
  width: 100%;
  max-width: 800px;
  min-width: 600px;
  font-size: 9px;
  background-color: #222;
  border: 1px solid #333;
  color: white;
  text-shadow: 0 0 1px white;
  position: fixed;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  box-sizing:border-box;
  padding: 5px;
  margin: 0 auto;
}

.AppComponent .BottomButton.ToggleViewButton {
  display: table;
  position: fixed;
  bottom: 7px;
  right: 5px;
  left: auto;
  top: auto;
  font-size: 25px;
  transition: background-color .2s linear, box-shadow .2s linear;
}

.AppComponent .BottomButton span {
  display: table-cell;
  vertical-align: top;
  width: 25px;
  height: 25px;
  color: white;
  text-shadow: 0 0 4px white;
  cursor: pointer;
  border: 0px solid #333;
  padding: 8px;
  border-radius:50%;
  background-color: #333;
}

.AppComponent .BottomButton span:hover {
  background-color: #444;
  box-shadow: 0 0 4px white;
}

.AppComponent .Error {
  position: fixed;
  top: auto;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: red;
  color: yellow;
  cursor: pointer;
  text-shadow: 0 0 2px yellow;
}

.AppComponent .Viewer {
  text-align: left;
  color: white;
}

</style>
